*
{
    padding: 0;
    margin: 0;
}
.contact{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background-color: lightskyblue;
    width: 100%;
}
.contactmoi{

    
    font-size: 2em;
    text-align: center;
    border-radius: 20px;
    width: 99%;
    color: azure;
    p
    {
        font-family: title;
    }
}
.contacticons a{
    color: azure;
}


.contacticons {
    display: flex;
}
.emailed{
    font-size: 2em;
}
.footer{
    width: 100%;
    text-align: center;
    border-top: solid orange 2px;
    padding-top: 1%;
    padding-bottom: 1%;
    p
    {
        color: azure;
    }
}


/*  <---- Media Queries ----> */

/*  <---- Mobile Landscape ----> */

@media screen and (max-width: 666px) {
    .contact{
        height: 100vh;
    }
    .contactmoi
    {
        font-size: 2em;
        background-color: azure;
        font-family: title, 'Times New Roman', Times, serif;
        transition: 1.5s;
        background-image: linear-gradient(
            -75deg,
            transparent 0,
            transparent 5%,
            orange 5%,
            orange 10%,
            transparent 10%,
            transparent 100%
        );
        background-size: 200% 100%;
        color: transparent;
        background-clip: text;
    }
    .contactmoi:hover
    {
        background-position: -120% 0;
        transition: .5s;
    }
    .contacticons{
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
        align-items: center;
        height: 50%;
    }
    .emailed{
        font-size: 1.5em;
        
    }
    .linkedin{
        height: 50%;
    }
    .github{
        height: 50%;
    }

}
