.down{
    text-align: center;
}
ul, li {
    list-style: none;
}
p {
    font-family: header;
}

.about{
    height: 100%;
    width: 100%;
    
    background: linear-gradient( #3d72b4, lightskyblue),
}

.pics{
    border-radius: 50%;
}

.b1{
    display: flex;
    align-items: center;
    height: 20%;
    width: 100%;

}
.b2{
    height: 50%;
    
}
.b2a {
    text-align: center;
    
}

.b2a p ul {
    font-size: .7em;
}
.b2a h3 {
    font-family: header;
}

.pic{
    height: 45%;
    width: 45%;
    box-shadow: 0px 0px 18px -6px black;
}
.pic:hover{
    box-shadow: 0px 0px 40px -6px black;
}
.b1downicon{
    position: absolute;
    bottom: -90%;
    left: 49%;
    opacity: 0.5;

}
.buttons{
    border-radius: 10%;
    padding: 10%;
    background-color: beige;
    font-family: "Dosis";
    border: black solid 1px;
    cursor: pointer;
}

